
import Vue from "vue";
import store from "@/store";
import API from "@/api/API";
import { mask } from "vue-the-mask";

export default Vue.extend({
  name: "Create",

  components: {
    ata: () => import("../orderComponents/AtaEdit.vue"),
    aaij: () => import("../orderComponents/AAIJEdit.vue"),
    cert: () => import("../orderComponents/CERTEdit.vue"),
    eval: () => import("../orderComponents/EVALEdit.vue"),
    exp: () => import("../orderComponents/EXPEdit.vue")
  },

  directives: { mask },

  data: () => ({
    model: {} as any,
    loading: false as boolean,
    changeStatusDialog: false as boolean,
    editComponents: {
      1: "ata",
      2: "aaij",
      3: "cert",
      4: "eval",
      5: "exp"
    },
    breadcrumb: [
      {
        text: "orders.title",
        disabled: false,
        href: "/orders"
      },
      {
        text: "orders.form.header.edit",
        disabled: true,
        href: `/orders/edit`
      }
    ],
    orderStatuses: [] as Array<any>,
    selects: {
      statuses: [] as Array<any>
    } as any
  }),

  computed: {
    expanded() {
      return this.$store.getters["navigation/expanded"];
    }
  },

  async mounted() {
    await this.loadData();
  },

  methods: {
    async loadData() {
      this.loading = true;
      try {
        const params = {} as any;
        if (this.$route.query.hasOwnProperty("additions")) {
          params.additions = this.$route.query.additions;
        }

        const item = await API.orders().getForEdit(
          Number(this.$route.params.id),
          params
        );

        const statuses = await this.$API.orders().getStatuses();

        for (const item of statuses) {
          this.selects.statuses[item.alias] = item;
        }
        this.orderStatuses = await this.$API
          .orders()
          .getOrderStatuses(this.$route.params.id);

        this.$nextTick(() => {
          this.fullLoaded = true;
          this.model = item;
        });
      } catch (e) {
        await store.dispatch("alert/showError", e.message);
      }
      this.loading = false;
    },
    showStatusDialog(): void {
      this.changeStatusDialog = !this.changeStatusDialog;
    },
    async changeOrderStatus(status: any): Promise<void> {
      try {
        if (status.status) {
          await this.$API
            .orders()
            .changeOrderStatus(this.model.order.id, status.alias);
          this.changeStatusDialog = false;
          await this.loadData();
        }
      } catch (e) {
        await this.$store.dispatch("alert/showError", e.message);
      }
    },
    async cloneOrder(id: string): Promise<void> {
      try {
        await this.$API.orders().cloneOrder(id);
        this.$emit("update");
      } catch (e) {
        await this.$store.dispatch("alert/showError", e.message);
      }
    }
  }
});
